<template>
    <div class="news-info-list" @click="getRouter">
        <img v-lazy="new_item.ext[1].value[0].url" />
        <div class="item-content">
            <p style="font-weight: bold; font-size: 15px; padding-bottom: 8px">
                {{ new_item.title }}
            </p>
            <p style="font-size: 12px; padding-bottom: 5px">
                发布日期：{{
                    new_item.start_time.match(/\d{4}-\d{2}-\d{2}/)[0]
                }}
            </p>
            <div class="base-line"></div>
            <div class="item-intro">{{ new_item.intro }}</div>
        </div>
    </div>
</template>

<script>
import { useRouter } from "vue-router";
export default {
    props: {
        new_item: {
            type: Object,
            require: true,
        },
    },
    setup(props) {
        // console.log(props.new_item);
        const router = useRouter();
        const getRouter = () => {
            router.push({
                name: "newsDetail",
                query: {
                    contentId: props.new_item.contentId,
                },
            });
        };
        return { getRouter };
    },
};
</script>

<style lang="scss" scoped>
.news-info-list {
    width: 100%;
    margin-top: 20px;
    border-radius: 5px;
    border: 1px solid #3dc5eb;
    img {
        width: 100%;
    }
    .item-content {
        position: relative;
        color: #62eaf5;
        padding: 5px 5px 5px 10px;
        .base-line {
            position: absolute;
            width: calc(100% - 14px);
            left: 7px;
            height: 1px;
            background: rgba(98, 234, 245, 0.2);
        }
        .item-intro {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            font-size: 13px;
            color: white;
            padding-top: 5px;
        }
    }
}
</style>