<template>
    <page-view class="new-info">
        <div class="title">
            <span>作战资讯</span>
            <span> / </span>
            <span>NEWS</span>
        </div>
        <div class="news_nav">
            <div
                class="news_nav_list"
                v-for="(item, index) of newsTitle"
                :key="index"
                :class="{ news_nav_list_active: actived == index }"
                @click="changeInfo(item[1], index)"
            >
                {{ item[0] }}
            </div>
        </div>
        <div class="new-list">
            <NewsInfoList
                v-for="(new_item, index) of newsInfo"
                :key="index"
                :new_item="new_item"
            />
        </div>
        <div class="more-btn" @click="moreNews">查看更多</div>

        <page-footer />
    </page-view>
</template>
<script>
import Api from "@/utils/api";
import { ref, reactive, toRefs } from "vue";
import NewsInfoList from "../components/NewsInfoList.vue";
export default {
    name: "NewInfo",
    components: {
        NewsInfoList,
    },
    setup() {
        const actived = ref(0);
        const changeInfoStar = ref(true);
        const newsTitle = reactive([
            ["最新", "171"],
            ["动态", "172"],
            ["公告", "173"],
            ["活动", "174"],
            ["补给", "175"],
        ]);
        const infoDate = reactive({
            newsInfo: [],
            pageNum: 1,
        });

        const getNewInfo = async (id, pageNum) => {
            let results = await Api.getInfo.getInfos({
                pageSize: 10,
                pageNum: pageNum,
                channelId: id,
            });
            results.list.forEach((item) => {
                infoDate.newsInfo.push(item);
            });
            if (changeInfoStar.value) {
                changeInfo();
            }
        };
        getNewInfo(171, 1);
        const changeInfo = async (id, index) => {
            changeInfoStar.value = false;
            infoDate.pageNum = 1;
            infoDate.newsInfo = [];
            if (index != undefined) {
                actived.value = index;
            }
            if (id == undefined) {
                id = 171;
            }
            await getNewInfo(id, infoDate.pageNum);
            // console.log(infoDate.newsInfo);
        };
        const moreNews = () => {
            infoDate.pageNum += 1;
            // console.log(newsTitle[actived.value][1]);
            getNewInfo(newsTitle[actived.value][1], infoDate.pageNum);
        };
        return {
            newsTitle,
            actived,
            changeInfo,
            ...toRefs(infoDate),
            moreNews,
        };
    },
};
</script>

